main{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 80px;
  box-sizing: border-box;
}
.active-title:hover{
  text-decoration: underline;
}
.title{
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: 5px;
  margin: 20px;
}
.lighter-title {
  font-weight: lighter;
}
.container{
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
section{
  box-sizing: border-box;
  margin: 20px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  flex-basis: calc(33% - 2*20px);
  transition: all .5s ease-in-out;
  &:hover{
    box-shadow: 0 0 20px #555;
  }
}
@media screen and (max-width: 1200px) {
  main {
    padding: 40px;
  }
}
@media screen and (max-width: 700px) {
  main {
    padding: 20px;
  }
}

@media screen and (max-width: 900px){
  .container section {
    flex-basis: calc(50% - 2*20px);
  }
}
@media screen and (max-width: 600px){
  .container section {
    flex-basis: calc(100% - 2*20px);
  }
  .title{
    margin: 0 0 20px;
  }
}

.image-block {
  position: relative;
  background-color: white;
  height: calc(40vh + 1vw);
}
.out-of-stock .out-of-stock-div{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.image-block img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.out-of-stock img {
  filter: grayscale(100%);
  opacity: 0.5;
}
.product-price{
  font-family: Raleway, serif;
  font-weight: 600;
}

.add-to-cart-button{
  width: 48px;
  height: 48px;
  //padding: 15px;
  border-radius: 50%;
  position: absolute;
  right: 0;
  bottom: 0;
  transform: translate(-50%, 50%);
  background-color: #5ECE7B;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  border: none;
  &:active{
    filter: brightness(80%);
  }
}
.out-of-stock .add-to-cart-button{
  display: none;
}

section:hover .add-to-cart-button{
  visibility: visible;
}
